@import url('https://fonts.googleapis.com/css?family=Jura');

*{
  margin: 0;
  padding: 0;
  font-family: 'Jura', sans-serif;
  font-weight: 100;
  color: #f4f4f4;

}

body{
  height: 100vh;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: black;
}

.title{
  text-align: center;
  font-size: calc(25px + 4vw);
  padding: 35px 2%;
}

form{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;

}

.search-bar{
  width: calc(160px + 30vw);
  font-size: calc(18px + .8vw);
  margin-right: 1vw;
  border: 1px solid #CCCCCC !important;
  color: black;


}

.submit-btn{
  width: calc(50px + 5vw);
  font-size: calc(10px + 1vw);
  color: black;
}

.results-box{
  margin: 2% 2%;
  border-radius: 10px;
  opacity: 0;
  -webkit-transition: opacity .2s ease-out;
  -o-transition: opacity .2s ease-out;
  transition: opacity .2s ease-out;

}

.show{
  opacity: 1;
}

.result{

  display: -ms-flexbox;

  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  margin: 20px 2%;
  text-align: justify;
  text-decoration: none;
  color: black;
}

.result-title{
  font-size: calc(14px + 1.5vw);
}

.description{
  font-size: calc(14px + .8vw);
}

.footer{
  text-align: center;
  margin-top: 30px;
  margin-bottom: 30px;

}


